import React from "react";

interface astroDataPops {
  astroData?: any;
}

const astroDataCompunent: React.FC<astroDataPops> = ({ astroData }) => {
  const displayItems = [
    { label: "නැකත", value: astroData.nakshatra },
    { label: "පාදය", value: `${astroData.nakshatra_pada} පාදය` },
    { label: "තිතිය", value: astroData.tithi },
    { label: "රාශිය", value: astroData.rasi },
    { label: "ගණය", value: astroData.gana },
    { label: "යෝනිය", value: astroData.yoni },
    { label: "වස්‍යය", value: astroData.vasya },
    { label: "නාඩිය", value: astroData.nadi },
    { label: "වර්ණය", value: astroData.varna },
    { label: "පායා", value: astroData.paya },
    { label: "තත්වය", value: astroData.tatva },
    { label: "ජීවන මැණික", value: astroData.life_stone },
    { label: "වාසනා මැණික", value: astroData.lucky_stone },
    { label: "ධන මැණික", value: astroData.fortune_stone },
    { label: "නම ආරම්භය", value: astroData.name_start },
    { label: "ලග්නය", value: astroData.ascendant_sign },
    { label: "ලග්න නැකත", value: astroData.ascendant_nakshatra },
    { label: "රාශි අධිපතිය", value: astroData.rasi_lord },
    { label: "නැකත් අධිපතිය", value: astroData.nakshatra_lord },
    { label: "සූර්ය රාශිය", value: astroData.sun_sign },
    { label: "කරණය", value: astroData.karana },
    { label: "යෝගය", value: astroData.yoga },
  ];
  return (
    <div className="w-full mt-5 mb-5 shadow py-8 rounded-2xl justify-start px-4 lg:px-0 lg:justify-center items-center flex bg-linear-to-r from-purple-600 to-indigo-600">
      <div className="grid lg:grid-cols-4 grid-cols-1 gap-8 justify-center items-center text-xl text-white font-bold">
        {displayItems.map((item, index) => (
          <span key={index} className="flex gap-3 items-center">
            <div className="w-3 h-3 bg-blue-50 rounded-full shrink-0"></div>
            <span>
              {item.label} : {item.value}
            </span>
          </span>
        ))}
      </div>
    </div>
  );
};

export default astroDataCompunent;
